import React, {useEffect} from "react";
import Loading from "./Loading";
import {useNavigate} from 'react-router-dom';


function NumberPage(props){
    document.title = "HM Shop | Not Found"
    let nav = useNavigate();
    useEffect(()=>{
            nav("/404");
    }, []);

    return (
        <>
           <Loading />
        </>
    )
}

export default NumberPage;